import React from "react";
import { Box, Button, List, ListItem, Typography } from "@mui/material";
import { useState } from "react";
import { useRef } from "react";
import { useStyles } from "./TodoStyle";
import AddCircleOutlineIcon from "@mui/icons-material/AddCircleOutline";
import InputLabel from "@mui/material/InputLabel";
import HighlightOffIcon from "@mui/icons-material/HighlightOff";
import ClearIcon from "@mui/icons-material/Clear";
import Dialog from "@mui/material/Dialog";
import ErrorOutlineIcon from "@mui/icons-material/ErrorOutline";

function TodoApp() {
  const classes = useStyles();
  const [open, setOpen] = useState(false);
  const [todoArray, setTodoArray] = useState([]);
  const [todo, setTodo] = useState("");
  const inpVal = useRef();

  const filterIt = (event) => {
    setTodo(event.target.value);
  };

  const clear = () => {
    setTodoArray([]);
  };

  const removeTodo = (id) => {
    const reducedArr = [...todoArray];
    reducedArr.splice(id, 1);
    setTodoArray(reducedArr);
  };

  const list = todoArray
    .filter((todos) => todos.toLowerCase().includes(todo.toLowerCase()))
    .map((filteredTodos, id) => (
      <ListItem key={id} className={classes.todoList}>
        <Typography>{filteredTodos}</Typography>
        <HighlightOffIcon
          onClick={() => removeTodo(id)}
          disableRipple
          color="error"
          sx={{ cursor: "pointer" }}
        />
      </ListItem>
    ));

  const addTodo = () => {
    let val = inpVal.current.value;

    if (val !== "") {
      setTodoArray((todoArray) => [...todoArray, val]);
      inpVal.current.value = "";
    } else {
      setOpen(true);
    }
  };

  return (
    <>
      <Box className={classes.TodoWrapper}>
        <Box className={classes.TodoBox}>
          <Box className={classes.todoHead}>
            <Typography variant="h6" sx={{ color: "white" }}>
              Todo App
            </Typography>
          </Box>
          <Box className={classes.fieldBoxes}>
            <Box className={classes.searchBox}>
              <InputLabel>Search Items</InputLabel>
              <input
                className={classes.addTodoInput}
                onChange={filterIt}
                value={todo}
                placeholder="Search items here..."
              />
              {todo !== "" ? (
                <ClearIcon
                  className={classes.clearBtn}
                  onClick={() => setTodo("")}
                />
              ) : null}
            </Box>
            <Box className={classes.addBox}>
              <Box className={classes.inputBox}>
                <InputLabel>Add New Item</InputLabel>
                <input
                  type="text"
                  className={classes.addTodoInput}
                  ref={inpVal}
                  placeholder="Add your todo"
                />
              </Box>

              <Button
                className={classes.addBtn}
                startIcon={<AddCircleOutlineIcon />}
                variant="contained"
                onClick={addTodo}
              >
                Add
              </Button>
            </Box>
          </Box>
          <Box className={classes.todoSec}>
            <List>{list}</List>
          </Box>
          <Box className={classes.lowerTodoSec}>
            {todo !== "" ? null : (
              <Typography>You have {todoArray.length} pending tasks</Typography>
            )}
            <Button
              variant="contained"
              onClick={clear}
              className={classes.clearBtn2}
            >
              clear
            </Button>
          </Box>
        </Box>
      </Box>

      <Dialog open={open}>
        <Box className={classes.errorModal}>
          <ErrorOutlineIcon className={classes.warningIcon} />
          <Typography gutterBottom>
            Please enter the todo in input field
          </Typography>
          <Button
            variant="contained"
            sx={{ marginTop: "20px" }}
            onClick={() => setOpen(false)}
            disableRipple
          >
            OK
          </Button>
        </Box>
      </Dialog>
    </>
  );
}

export default TodoApp;
