import { makeStyles } from "@mui/styles";
import { theme } from "./Theme";

export const useStyles = makeStyles({
  TodoWrapper: {
    padding: "8px",
  },

  TodoBox: {
    margin: "auto",
    maxWidth: "500px",
    padding: "14px",
    borderRadius: "5px",
    outline: "1px solid rgba(0, 0, 0, 0.23)",
    overflow: "hidden",
    position: "relative",
    marginTop: "35px",

    [theme.breakpoints.down("sm")]: {
      marginTop: "13px",
    },
  },

  todoHead: {
    position: "absolute",
    top: "-6px",
    left: "-3px",
    right: "-3px",
    background: "#2ec720",
    padding: "14px 20px",
  },

  fieldBoxes: {
    marginBottom: "20px",
    marginTop: "55px",
  },

  todoList: {
    width: "100%",
    justifyContent: "space-between",
    border: "1px solid rgba(0, 0, 0, 0.23)",
    padding: "10px 10px",
    marginBottom: "14px",
    borderRadius: "5px",

    "&:hover": {
      backgroundColor: "rgba(0,0,0,.03)",
    },
  },

  addBox: {
    display: "flex",
    marginTop: "20px",

    [theme.breakpoints.down("sm")]: {
      flexDirection: "column",
    },
  },

  searchBox: {
    width: "100%",
    display: "flex",
    flexDirection: "column",
    position: "relative",
    marginBottom: "22px",
  },

  inputBox: {
    display: "flex",
    flexDirection: "column",
    width: "100%",
  },

  addTodoInput: {
    border: "1px solid rgba(0, 0, 0, 0.23)",
    outline: "none",
    padding: "13px",
    borderRadius: "5px",
    fontSize: "14px",
    marginTop: "8px",
    transition: "all 0.2s",
    height: "17px",

    "&:focus": {
      boxShadow: "#e6f2f2 0 0 0 0.2rem",
      borderColor: "#66eded",
    },

    "&::placeholder": {
      color: "rgba(0, 0, 0, 0.23)",
    },
  },

  addBtn: {
    whiteSpace: "nowrap",
    alignSelf: "center",
    marginLeft: "17px",
    letterSpacing: "1px",
    background: "#2ec720",
    marginTop: "28px",
    width: "96px",

    "&:hover": {
      background: "#2ec720",
    },

    [theme.breakpoints.down("sm")]: {
      marginTop: "23px",
    },
  },

  errorModal: {
    padding: "20px",
    textAlign: "center",
  },

  clearBtn: {
    position: "absolute",
    right: "9px",
    top: "41px",
    cursor: "pointer",
  },

  lowerTodoSec: {
    display: "flex",
    width: "100%",
    justifyContent: "space-between",
    alignItems: "end",
    marginTop: "14px",
  },

  clearBtn2: {
    textTransform: "capitalize",
    fontSize: "17px",
    padding: "2px 10px",
    letterSpacing: "1.2px",
  },

  warningIcon: {
    fontSize: "51px",
    marginBottom: "8px",
    color: "red",
  },
});
